import React, {useEffect} from "react";
import enJson from './locales/en.json'
import jaJson from './locales/ja.json'
import i18n from 'i18next'
import {initReactI18next} from "react-i18next";
import {useTranslation} from "react-i18next";
import {AppBar, Button, makeStyles, Toolbar, Typography} from "@material-ui/core";
import Box from "@material-ui/core/Box";
import {RouteComponentProps} from "react-router-dom";

i18n.use(initReactI18next).init({
    resources: {
        en: {translation: enJson},
        ja: {translation: jaJson}
    },
    lng: 'ja',
    fallbackLng: 'ja',
    interpolation: {escapeValue: false}
}).then()

const About: React.FC<RouteComponentProps> = (props: RouteComponentProps) => {
    const useStyles = makeStyles(() => ({
        container: {
            margin: '0 auto',
            padding: '20px',
            maxWidth: '500px'
        },
        link: {
            margin: '20px 0'
        }
    }))
    const classes = useStyles()

    const [t, i18n] = useTranslation()

    useEffect(() => {
        if (localStorage.lang) {
            i18n.changeLanguage(localStorage.lang).then()
        } else {
            i18n.changeLanguage('ja').then()
        }
    }, [i18n])

    return (
        <div>
            <AppBar position={"relative"}>
                <Toolbar>
                    <Typography>{t('')}</Typography>
                </Toolbar>
            </AppBar>
            <Box className={classes.container} maxWidth={"xs"} display={"flex"} flexDirection={"column"}>
                <Box display={"flex"}>
                    <Button variant='contained' onClick={() => props.history.push('/')}>
                        {t('')}
                    </Button>
                </Box>
                <Box display={"flex"} justifyContent={"center"}>
                    <Typography variant='h3'>{t('')}</Typography>
                </Box>
                <Box className={classes.link} display={"flex"} justifyContent={"center"}>
                    <Typography variant='body1'>
                        {t('')}
                        <br/><br/>
                        {t('')}
                        <br/><br/>
                        {t('')}
                        <br/><br/>
                        {t('')}
                    </Typography>
                </Box>
                <Box className={classes.link} display={"flex"} justifyContent={"center"}>
                    <Typography variant='h5'>{t('')}</Typography>
                </Box>
                <Box className={classes.link} display={"flex"}>
                    <Typography variant='h6' align='left'>{t('')}</Typography>
                </Box>
                <Box className={classes.link} display={"flex"}>
                    <Typography variant='body1'>
                        {t('')}<br/>
                        {t('')}<br/>
                        <ul>
                            <li>{t('U = (), D = (), R = (), L = (), F = (), B = ()')}</li>
                            <li>{t('U90')}</li>
                            <li>{t('U\'90')}</li>
                            <li>{t('U2180')}</li>
                        </ul>
                        {t('')}
                        {t('')}<br/>
                        {t('')}: <a href={t('URL')}>{t('')}</a>
                    </Typography>
                </Box>
                <Box className={classes.link} display={"flex"}>
                    <Typography variant='h6' align='left'>{t('')}</Typography>
                </Box>
                <Box className={classes.link} display={"flex"}>
                    <Typography variant='body1'>
                        {t('')}<br/>
                        <br/>
                        {t('')}
                    </Typography>
                </Box>
            </Box>
        </div>
    )
}

export default About